<template>
	<div id="editDutyInfo">
		<div class="page_title">
			<router-link :to="{path:'/shop/staff',query:{name:choosePoMa}}">
				<span>职级管理</span>
			</router-link>
			<span>></span>
			<span>添加职位</span>
		</div>
		<div class="page_card">
			<div class="from_card">
				<el-form ref="form" :model="form" label-width="100px" size="mini" :rules="rules">
					<el-form-item label="职位名称" prop="position_name">
						<span v-if="form.entry_type=='1'">{{form.position_name}}</span>
						<el-input v-model="form.position_name" v-else></el-input>
					</el-form-item>
					
					<el-form-item label="权限" prop="right">
						<el-radio-group v-model="form.right" fill="#4877E7" @change="changeSelect">
							<el-radio-button v-for="item in radioRight" :label="item.label" :key="item.label"></el-radio-button>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="" v-if="isBackAdmin==true">
						<el-checkbox-group v-model="form.backRight" @change="getBackRight">
						<el-checkbox  v-model="isOff"  label="后台全部权限" name="backRight">启用</el-checkbox>
						</el-checkbox-group>
					</el-form-item>
					<div v-if="isBackAdmin==false">
						<el-form-item label="">
							<el-checkbox v-model="isCheckAll" @change="handleCheckAllChange(isCheckAll)">全选</el-checkbox>
						</el-form-item>
						<el-checkbox-group v-model="form.arrRight" @change="handleCheckedCitiesChange">
							<el-form-item label="">
								<p class="blooder">点餐系统</p>
								<el-checkbox label="使用点餐" name="arrRight"></el-checkbox>
								<el-checkbox label="赠送" name="arrRight"></el-checkbox>
								<el-checkbox label="结账" name="arrRight"></el-checkbox>
							</el-form-item>
							<el-form-item label="">
								<p class="blooder">用户请求管理</p>
								<p>新订单管理</p>
								<el-checkbox label="使用新订单管理" name="arrRight"></el-checkbox>
								<el-checkbox label="下单并结账" name="arrRight"></el-checkbox>
								<el-checkbox label="关闭并退款" name="arrRight"></el-checkbox>
							</el-form-item>
							<el-form-item label="" class="po-relative">
								<p>退款申请管理</p>
								<el-checkbox label="使用退款申请" name="arrRight"></el-checkbox>
								<div class="line"></div>
							</el-form-item>
							<el-form-item label="" class="po-relative">
								<p class="blooder">预定系统</p>
								<el-checkbox label="使用预定" name="arrRight"></el-checkbox>
								<div class="line"></div>
							</el-form-item>
							<el-form-item label="" class="po-relative">
								<p class="newOrder blooder">历史订单管理</p>
								<el-checkbox label="使用历史订单" name="arrRight"></el-checkbox>
								<el-checkbox label="结账(历史订单)" name="arrRight"></el-checkbox>
								<el-checkbox label="反结账" name="arrRight"></el-checkbox>
								<el-checkbox label="退款" name="arrRight"></el-checkbox>
								<el-checkbox label="关闭订单" name="arrRight"></el-checkbox>
								<el-checkbox label="开发票" name="arrRight"></el-checkbox>
								<el-checkbox label="红冲" name="arrRight"></el-checkbox>
								<div class="line"></div>
							</el-form-item>
							<el-form-item label="">
								<p class="newOrder blooder">收银系统</p>
								<el-checkbox label="使用收银" name="arrRight"></el-checkbox>
								<el-checkbox label="挂账" name="arrRight"></el-checkbox>
								<el-checkbox label="抹零" name="arrRight"></el-checkbox>
								<!--<el-checkbox label="后台全部权限" name="arrRight"></el-checkbox>-->
								<!--<el-checkbox label="前端权限" name="arrRight"></el-checkbox>-->
							</el-form-item>
						</el-checkbox-group>
					</div>
				</el-form>
				<!--<div class="line" v-if="form.right=='点餐收银'"></div>-->
			</div>
			<div class="btnGroup">
				<el-button class="save" @click="saveStaffInfo('form')">保存</el-button>
				<el-button class="saveAdd" @click="saveAddStaffInfor('form')" v-if="$route.query.infor == undefined">保存并继续添加</el-button>
				<el-button class="cancel" @click="cancelStaffInfor('form')">取消</el-button>
			</div>
		</div>
	</div>
</template>

<script>
import { staffDataGet } from "../../api";
import { treeMethods } from "../../index";
import { positionRight } from "@/config/cfg";
const rightOptions = [
  "使用点餐",
  "赠送",
  "结账",
  "使用新订单管理",
  "下单并结账",
  "关闭并退款",
  "使用退款申请",
  "使用预定",
  "使用历史订单",
  "结账(历史订单)",
  "反结账",
  "退款",
  "关闭订单",
  "开发票",
  "红冲",
  "使用收银",
  "挂账",
  "抹零"
];
export default {
  data() {
    return {
      choosePoMa: false, //返回职级列表
      isBackAdmin: false, //权限是否是后台管理
      isOff: false,
      webRight: "全部",
      dutyNameArr: [], //职位名称数组，判断职位名称是否重复
      initPosition: "", //输入框初始化内容，用来判断输入框的值是否发生改变
      isCheckAll: false, //控制全选
      isIndeterminate: true, //启用不启用
      form: {
        //表单数据
        position_name: "", //职位名称
        entry_type: "", //是否是系统默认职位 1 是 2 手动
        right: "点餐收银", //权限
        type: [], //启用
        backRight: [],
        arrRight: [], //权限数组
        newOrder: [], //订单权限
        refund: [] //退款申请
      },
      rules: {
        position_name: [
          {
            required: true,
            message: "请输入职位",
            trigger: "click"
          }
        ],
        right: [
          {
            required: true,
            message: "请选择部门",
            trigger: "change"
          }
        ]
      },
      radioRight: [
        {
          label: "点餐收银"
        },
        {
          label: "后台管理"
        }
      ]
    };
  },
  created() {
    this.initData();
  },
  methods: {
    //初始化数据
    initData() {
      if (
        this.$route.query.infor !== undefined &&
        this.$route.query.infor !== "[object Object]"
      ) {
        this.form.position_id = this.$route.query.infor.position_id;
        this.form.position_name = this.$route.query.infor.position_name;
        this.form.entry_type = this.$route.query.infor.entry_type;
        this.form.arrRight = this.$route.query.infor.arrRight;
        this.initPosition = this.form.position_name;
        sessionStorage.setItem("arrRight", this.form.position_id);
      } else if (this.$route.query.infor !== undefined) {
        this.form.position_id = sessionStorage.getItem("arrRight");
      }

      if (this.$route.query.all !== undefined) {
        this.dutyNameArr = this.$route.query.all;
      } else {
        this.getAllDutyName();
      }
      let data = {
        get_position_info: 1, //参数
        position_id: this.form.position_id //(string)职位id
      };
      staffDataGet.getDutyList(data, resp => {
        let formData = resp.data.position_info;
        this.form.position_name = formData.position_name;
        this.form.entry_type = formData.entry_type;
        this.form.arrRight = this.isRight(resp.data.permission);
        this.initPosition = this.form.position_name;
        if (this.form.arrRight.length == rightOptions.length) {
          this.isCheckAll = true;
        }
        if (
          this.form.arrRight.indexOf(
            positionRight.code[positionRight.ALLWEB]
          ) !== -1
        ) {
          this.isCheckAll = true;
          this.form.arrRight = rightOptions;
        }
      });
    },

    //返回职级管理页面
    backToPoMa() {
      this.$emit("choosePoMa", this.choosePoMa);
      this.$router.push({
        path: "/shop/staff",
        query: {
          name: this.choosePoMa
        }
      });
    },

    //权限的选择且切换   前后台
    changeSelect(label) {
      if (this.radioRight[0].label == label) {
        this.isBackAdmin = false;
      } else {
        this.isBackAdmin = true;
      }
    },
    //			分解成权限
    isRight(permission) {
      var arr = [];
      var num;
      for (var i in positionRight) {
        permission.forEach(item => {
          if (item == positionRight[i]) {
            if (item == positionRight.ALLBACKSTAGE) {
              this.form.backRight.push(positionRight.code[item]);
            }
            arr.push(positionRight.code[positionRight[i]]);
          }
        });
      }

      return arr;
    },
    //点击保存
    saveStaffInfo(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (
            this.initPosition !== this.form.position_name &&
            this.dutyNameArr.indexOf(this.form.position_name) > -1
          ) {
            this.$slnotify({
              message: "该职位已经存在"
            });
            this.form.position_name = "";
            return false;
          } else {
            this.getRightList();
            if (
              this.$route.query.all !== undefined ||
              this.$route.query.infor !== undefined
            ) {
              this.$router.go(-1);
            } else {
              this.backToPoMa();
            }
          }
        }
      });
    },
    //控制全选
    handleCheckAllChange(val) {
      this.form.arrRight = val ? rightOptions : [];
    },
    //控制单个复选框
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.isCheckAll = checkedCount === rightOptions.length;
    },
    //启用后台权限
    getBackRight() {
      if (this.form.backRight !== []) {
        this.form.arrRight.push(this.form.backRight);
      }
    },
    //判断是否右前后台权限
    checkBackRight() {},
    //获取权限
    getRightList() {
      if (this.$route.query.infor !== undefined) {
        var a = [];
        if (
          this.form.arrRight.length == rightOptions.length &&
          this.form.backRight == []
        ) {
          a.push(2);
        } else if (
          this.form.backRight !== [] &&
          this.form.arrRight.length == rightOptions.length
        ) {
          a.push(1);
          a.push(2);
        } else {
          a = treeMethods.judgeStaffList(this.form.arrRight, 1);
        }
        let data = {
          save_position: 1,
          position_id: this.form.position_id, //新建时不传
          position_name: this.form.position_name,
          position_permission: a
        };
        staffDataGet.editDutyRight(data);
      } else {
        var a = [];
        if (this.form.arrRight.length == rightOptions.length &&
          this.form.backRight == []) {
          a.push(2);
        } else if ( this.form.backRight !== [] &&
          this.form.arrRight.length == rightOptions.length) {
          a.push(1);
          a.push(2);
        } else {
          a = treeMethods.judgeStaffList(this.form.arrRight, 1);
        }
        let data = {
          save_position: 1,
          position_name: this.form.position_name,
          position_permission: a
        };

        staffDataGet.editDutyRight(data);
      }
    },

    //获取职位列表
    getAllDutyName() {
      let data = {
        get_position_list: 1
      };
      staffDataGet.getDutyList(data, resp => {
        resp.data.position_list.forEach(item => {
          if (this.dutyNameArr.indexOf(item.position_name) == -1) {
            this.dutyNameArr.push(item.position_name);
          }
        });
      });
    },

    //点击保存并继续添加
    saveAddStaffInfor(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (
            this.initPosition !== this.form.position_name &&
            this.dutyNameArr.indexOf(this.form.position_name) > -1
          ) {
            this.$slnotify({
              message: "该职位已经存在"
            });
            this.form.position_name = "";
            return false;
          } else {
            this.getRightList();
            this.isCheckAll = false;
            this.handleCheckAllChange("");
            this.$refs[formName].resetFields();
          }
        }
      });
    },

    //点击取消
    cancelStaffInfor(formName) {
      this.isCheckAll = false;
      if (this.$route.query.all !== undefined) {
        this.$router.go(-1);
      } else {
        this.backToPoMa();
      }
      this.handleCheckAllChange("");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.page_card {
  width: 100%;
  min-height: 580px;
  padding-bottom: 60px;
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.page_title {
  @include wh(100%, 40px);
  @include fc(12px, #333333);
  margin-bottom: 14px;
  line-height: 40px;
}

.from_card {
  width: 45%;
  padding: 48px 0 40px 0;
  margin-left: 133px;
}

.dash:after {
  content: "";
  width: 100%;
  position: absolute;
  top: 1%;
  left: 0;
  border-bottom: 1px dashed lightgray;
}
.po-relative {
  position: relative;
}
.line:after {
  content: "";
  width: 100%;
  position: absolute;
  top: 110%;
  left: 0px;
  border-bottom: 1px solid lightgray;
}

p {
  height: 25px;
}

.title {
  margin-top: 20px;
}

.refondTitle {
  margin-top: 30px;
}
.blooder {
  font-size: 14px;
  color: #333333;
  font-weight: 900;
}
.btnGroup {
  min-width: 800px;
  margin-top: 30px;
}
</style>
<style lang="scss">
@import "src/styles/mixin.scss";
#editDutyInfo {
  .el-input {
    width: 320px;
  }
  .el-input__inner {
    border-radius: 0;
    height: 34px;
  }
  .el-input__inner:hover {
    border: 1px solid #bfcbd9;
  }
  .el-input__inner:focus {
    border: 1px solid #bfcbd9;
  }
  .el-checkbox__label {
    color: #000000;
    opacity: 0.57;
  }
  .btnGroup {
    .el-button {
      width: 160px;
      height: 40px;
      @include fc(14px, #ffffff);
      border: none;
      margin-left: 20px;
      background: #4877e7;
      cursor: pointer;
    }
    .save {
      margin-left: 30%;
    }
    .cancel {
      background: #ffffff;
      color: #4877e7;
      border: 1px solid #4877e7;
    }
  }
  .el-radio-button__inner {
    width: 140px;
    height: 40px;
    line-height: 20px;
  }
  .el-input.el-input-group__append {
    border: 1px solid #ff4949;
  }
}
</style>


